'use client'
import { 
    Plus, 
    Users, 
    Calendar, 
    Settings, 
    Eye, 
    EyeOff, 
    Edit, 
    Trash2, 
    CheckCircle, 
    XCircle,
    Search,
    Filter,
    Download,
    BarChart3
  } from 'lucide-react';
import { useState } from 'react';
  
  const ElectionCommissionDashboard = () => {
    const [activeTab, setActiveTab] = useState('dashboard');
    const [elections, setElections] = useState([
      {
        id: 1,
        title: "Presidential Election 2024",
        description: "National Presidential Election",
        startTime: "2024-11-01T08:00",
        endTime: "2024-11-01T18:00",
        isActive: true,
        totalVotes: 1250,
        candidatesCount: 4
      },
      {
        id: 2,
        title: "Local Council Election",
        description: "Municipal Council Election",
        startTime: "2024-12-15T09:00",
        endTime: "2024-12-15T17:00",
        isActive: false,
        totalVotes: 0,
        candidatesCount: 8
      }
    ]);
  
    const [candidates, setCandidates] = useState([
      {
        id: 1,
        name: "John Smith",
        partyName: "Democratic Party",
        symbol: "🐴",
        imageHash: "QmX123...",
        isActive: true,
        voteCount: 450,
        electionId: 1
      },
      {
        id: 2,
        name: "Sarah Johnson",
        partyName: "Republican Party", 
        symbol: "🐘",
        imageHash: "QmY456...",
        isActive: true,
        voteCount: 380,
        electionId: 1
      },
      {
        id: 3,
        name: "Mike Wilson",
        partyName: "Independent",
        symbol: "⭐",
        imageHash: "QmZ789...",
        isActive: false,
        voteCount: 120,
        electionId: 1
      }
    ]);
  
    const [newElection, setNewElection] = useState({
      title: '',
      description: '',
      startTime: '',
      endTime: ''
    });
  
    const [newCandidate, setNewCandidate] = useState({
      name: '',
      partyName: '',
      symbol: '',
      imageHash: '',
      electionId: ''
    });
  
    const [searchTerm, setSearchTerm] = useState('');
    const [filterStatus, setFilterStatus] = useState('all');
  
    // Dashboard Overview Component
    const DashboardOverview = () => (
      <div className="space-y-6">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
          <div className="bg-white rounded-lg shadow-md p-6 border-l-4 border-blue-500">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Total Elections</p>
                <p className="text-2xl font-bold text-gray-900">{elections.length}</p>
              </div>
              <Calendar className="h-8 w-8 text-blue-500" />
            </div>
          </div>
          
          <div className="bg-white rounded-lg shadow-md p-6 border-l-4 border-green-500">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Active Elections</p>
                <p className="text-2xl font-bold text-gray-900">
                  {elections.filter(e => e.isActive).length}
                </p>
              </div>
              <CheckCircle className="h-8 w-8 text-green-500" />
            </div>
          </div>
          
          <div className="bg-white rounded-lg shadow-md p-6 border-l-4 border-purple-500">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Total Candidates</p>
                <p className="text-2xl font-bold text-gray-900">{candidates.length}</p>
              </div>
              <Users className="h-8 w-8 text-purple-500" />
            </div>
          </div>
          
          <div className="bg-white rounded-lg shadow-md p-6 border-l-4 border-orange-500">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Total Votes</p>
                <p className="text-2xl font-bold text-gray-900">
                  {elections.reduce((sum, e) => sum + e.totalVotes, 0)}
                </p>
              </div>
              <BarChart3 className="h-8 w-8 text-orange-500" />
            </div>
          </div>
        </div>
  
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          <div className="bg-white rounded-lg shadow-md p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">Recent Elections</h3>
            <div className="space-y-3">
              {elections.slice(0, 3).map(election => (
                <div key={election.id} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                  <div>
                    <p className="font-medium text-gray-900">{election.title}</p>
                    <p className="text-sm text-gray-600">{election.totalVotes} votes cast</p>
                  </div>
                  <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                    election.isActive ? 'bg-green-100 text-green-800' : 'bg-gray-100 text-gray-800'
                  }`}>
                    {election.isActive ? 'Active' : 'Inactive'}
                  </span>
                </div>
              ))}
            </div>
          </div>
  
          <div className="bg-white rounded-lg shadow-md p-6">
            <h3 className="text-lg font-semibold text-gray-900 mb-4">Top Candidates</h3>
            <div className="space-y-3">
              {candidates
                .sort((a, b) => b.voteCount - a.voteCount)
                .slice(0, 3)
                .map(candidate => (
                  <div key={candidate.id} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                    <div className="flex items-center space-x-3">
                      <span className="text-2xl">{candidate.symbol}</span>
                      <div>
                        <p className="font-medium text-gray-900">{candidate.name}</p>
                        <p className="text-sm text-gray-600">{candidate.partyName}</p>
                      </div>
                    </div>
                    <span className="font-bold text-blue-600">{candidate.voteCount} votes</span>
                  </div>
                ))
              }
            </div>
          </div>
        </div>
      </div>
    );
  
    // Election Management Component
    const ElectionManagement = () => (
      <div className="space-y-6">
        <div className="flex justify-between items-center">
          <h2 className="text-2xl font-bold text-gray-900">Election Management</h2>
          <button 
            onClick={() => setActiveTab('create-election')}
            className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 flex items-center space-x-2"
          >
            <Plus className="h-4 w-4" />
            <span>Create Election</span>
          </button>
        </div>
  
        <div className="bg-white rounded-lg shadow-md overflow-hidden">
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Election Details
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Duration
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Status
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Votes
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {elections.map(election => (
                  <tr key={election.id}>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div>
                        <div className="text-sm font-medium text-gray-900">{election.title}</div>
                        <div className="text-sm text-gray-500">{election.description}</div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      <div>
                        <div>Start: {new Date(election.startTime).toLocaleDateString()}</div>
                        <div>End: {new Date(election.endTime).toLocaleDateString()}</div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${
                        election.isActive 
                          ? 'bg-green-100 text-green-800' 
                          : 'bg-red-100 text-red-800'
                      }`}>
                        {election.isActive ? 'Active' : 'Inactive'}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {election.totalVotes}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                      <div className="flex space-x-2">
                        <button 
                          onClick={() => toggleElectionStatus(election.id)}
                          className={`${
                            election.isActive ? 'text-red-600 hover:text-red-900' : 'text-green-600 hover:text-green-900'
                          }`}
                        >
                          {election.isActive ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
                        </button>
                        <button className="text-indigo-600 hover:text-indigo-900">
                          <Edit className="h-4 w-4" />
                        </button>
                        <button className="text-red-600 hover:text-red-900">
                          <Download className="h-4 w-4" />
                        </button>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    );
  
    // Candidate Management Component
    const CandidateManagement = () => {
      const filteredCandidates = candidates.filter(candidate => {
        const matchesSearch = candidate.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
                             candidate.partyName.toLowerCase().includes(searchTerm.toLowerCase());
        const matchesFilter = filterStatus === 'all' || 
                             (filterStatus === 'active' && candidate.isActive) ||
                             (filterStatus === 'inactive' && !candidate.isActive);
        return matchesSearch && matchesFilter;
      });
  
      return (
        <div className="space-y-6">
          <div className="flex justify-between items-center">
            <h2 className="text-2xl font-bold text-gray-900">Candidate Management</h2>
            <button 
              onClick={() => setActiveTab('add-candidate')}
              className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 flex items-center space-x-2"
            >
              <Plus className="h-4 w-4" />
              <span>Add Candidate</span>
            </button>
          </div>
  
          <div className="flex flex-col sm:flex-row gap-4">
            <div className="relative flex-1">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
              <input
                type="text"
                placeholder="Search candidates..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent w-full"
              />
            </div>
            <div className="relative">
              <Filter className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
              <select
                value={filterStatus}
                onChange={(e) => setFilterStatus(e.target.value)}
                className="pl-10 pr-8 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              >
                <option value="all">All Status</option>
                <option value="active">Active</option>
                <option value="inactive">Inactive</option>
              </select>
            </div>
          </div>
  
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredCandidates.map(candidate => (
              <div key={candidate.id} className="bg-white rounded-lg shadow-md p-6">
                <div className="flex items-center justify-between mb-4">
                  <div className="flex items-center space-x-3">
                    <span className="text-3xl">{candidate.symbol}</span>
                    <div>
                      <h3 className="font-semibold text-gray-900">{candidate.name}</h3>
                      <p className="text-sm text-gray-600">{candidate.partyName}</p>
                    </div>
                  </div>
                  <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                    candidate.isActive ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
                  }`}>
                    {candidate.isActive ? 'Active' : 'Inactive'}
                  </span>
                </div>
                
                <div className="space-y-2 mb-4">
                  <div className="flex justify-between text-sm">
                    <span className="text-gray-600">Vote Count:</span>
                    <span className="font-medium">{candidate.voteCount}</span>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span className="text-gray-600">Image Hash:</span>
                    <span className="font-mono text-xs">{candidate.imageHash.substring(0, 10)}...</span>
                  </div>
                </div>
                
                <div className="flex space-x-2">
                  <button 
                    onClick={() => toggleCandidateStatus(candidate.id)}
                    className={`flex-1 px-3 py-2 rounded-lg text-sm font-medium ${
                      candidate.isActive 
                        ? 'bg-red-100 text-red-800 hover:bg-red-200' 
                        : 'bg-green-100 text-green-800 hover:bg-green-200'
                    }`}
                  >
                    {candidate.isActive ? 'Deactivate' : 'Activate'}
                  </button>
                  <button className="px-3 py-2 bg-gray-100 text-gray-800 rounded-lg hover:bg-gray-200">
                    <Edit className="h-4 w-4" />
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      );
    };
    const CreateElectionForm = () => (
        <div className="max-w-2xl mx-auto">
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-2xl font-bold text-gray-900 mb-6">Create New Election</h2>
            
            <div className="space-y-6">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Election Title
                </label>
                <input
                  type="text"
                  value={newElection.title}
                  onChange={(e) => setNewElection({...newElection, title: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  required
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Description
                </label>
                <textarea
                  value={newElection.description}
                  onChange={(e) => setNewElection({...newElection, description: e.target.value})}
                  rows="3"
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  required
                />
              </div>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Start Time
                  </label>
                  <input
                    type="datetime-local"
                    value={newElection.startTime}
                    onChange={(e) => setNewElection({...newElection, startTime: e.target.value})}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    required
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    End Time
                  </label>
                  <input
                    type="datetime-local"
                    value={newElection.endTime}
                    onChange={(e) => setNewElection({...newElection, endTime: e.target.value})}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    required
                  />
                </div>
              </div>
              
              <div className="flex space-x-4">
                <button
                  type="button"
                  onClick={handleCreateElection}
                  className="flex-1 bg-blue-600 text-white py-2 px-4 rounded-lg hover:bg-blue-700 font-medium"
                >
                  Create Election
                </button>
                <button
                  type="button"
                  onClick={() => setActiveTab('elections')}
                  className="flex-1 bg-gray-300 text-gray-700 py-2 px-4 rounded-lg hover:bg-gray-400 font-medium"
                >
                  Cancel
                </button>
              </div>
            </div>
          </div>
        </div>
      );
    
  
    // Add Candidate Form
    const AddCandidateForm = () => (
      <div className="max-w-2xl mx-auto">
        <div className="bg-white rounded-lg shadow-md p-6">
          <h2 className="text-2xl font-bold text-gray-900 mb-6">Add New Candidate</h2>
          
          <form onSubmit={handleAddCandidate} className="space-y-6">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Election
              </label>
              <select
                value={newCandidate.electionId}
                onChange={(e) => setNewCandidate({...newCandidate, electionId: e.target.value})}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                required
              >
                <option value="">Select Election</option>
                {elections.map(election => (
                  <option key={election.id} value={election.id}>
                    {election.title}
                  </option>
                ))}
              </select>
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Candidate Name
              </label>
              <input
                type="text"
                value={newCandidate.name}
                onChange={(e) => setNewCandidate({...newCandidate, name: e.target.value})}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                required
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Party Name
              </label>
              <input
                type="text"
                value={newCandidate.partyName}
                onChange={(e) => setNewCandidate({...newCandidate, partyName: e.target.value})}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                required
              />
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Symbol (Emoji)
                </label>
                <input
                  type="text"
                  value={newCandidate.symbol}
                  onChange={(e) => setNewCandidate({...newCandidate, symbol: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="🐴"
                  required
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Image Hash (IPFS)
                </label>
                <input
                  type="text"
                  value={newCandidate.imageHash}
                  onChange={(e) => setNewCandidate({...newCandidate, imageHash: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  placeholder="QmX123..."
                  required
                />
              </div>
            </div>
            
            <div className="flex space-x-4">
              <button
                type="submit"
                className="flex-1 bg-blue-600 text-white py-2 px-4 rounded-lg hover:bg-blue-700 font-medium"
              >
                Add Candidate
              </button>
              <button
                type="button"
                onClick={() => setActiveTab('candidates')}
                className="flex-1 bg-gray-300 text-gray-700 py-2 px-4 rounded-lg hover:bg-gray-400 font-medium"
              >
                Cancel
              </button>
            </div>
          </form>
        </div>
      </div>
    );
  
    // Event Handlers
    const handleCreateElection = (e) => {
      e.preventDefault();
      const newElectionData = {
        id: elections.length + 1,
        ...newElection,
        isActive: true,
        totalVotes: 0,
        candidatesCount: 0
      };
      setElections([...elections, newElectionData]);
      setNewElection({ title: '', description: '', startTime: '', endTime: '' });
      setActiveTab('elections');
    };
  
    const handleAddCandidate = (e) => {
      e.preventDefault();
      const newCandidateData = {
        id: candidates.length + 1,
        ...newCandidate,
        isActive: true,
        voteCount: 0,
        electionId: parseInt(newCandidate.electionId)
      };
      setCandidates([...candidates, newCandidateData]);
      setNewCandidate({ name: '', partyName: '', symbol: '', imageHash: '', electionId: '' });
      setActiveTab('candidates');
    };
  
    const toggleElectionStatus = (electionId) => {
      setElections(elections.map(election => 
        election.id === electionId 
          ? { ...election, isActive: !election.isActive }
          : election
      ));
    };
  
    const toggleCandidateStatus = (candidateId) => {
      setCandidates(candidates.map(candidate => 
        candidate.id === candidateId 
          ? { ...candidate, isActive: !candidate.isActive }
          : candidate
      ));
    };
  
    const renderContent = () => {
      switch(activeTab) {
        case 'dashboard':
          return <DashboardOverview />;
        case 'elections':
          return <ElectionManagement />;
        case 'candidates':
          return <CandidateManagement />;
        case 'create-election':
          return <CreateElectionForm />;
        case 'add-candidate':
          return <AddCandidateForm />;
        default:
          return <DashboardOverview />;
      }
    };
  
    return (
      <div className="min-h-screen bg-gray-100">
        {/* Header */}
        <header className="bg-white shadow-sm border-b">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="flex justify-between items-center py-6">
              <div className="flex items-center space-x-4">
                <div className="bg-blue-600 p-2 rounded-lg">
                  <Settings className="h-6 w-6 text-white" />
                </div>
                <div>
                  <h1 className="text-2xl font-bold text-gray-900">Election Commission Dashboard</h1>
                  <p className="text-sm text-gray-600">Manage elections, candidates, and voting processes</p>
                </div>
              </div>
              <div className="flex items-center space-x-4">
                <span className="bg-green-100 text-green-800 px-3 py-1 rounded-full text-sm font-medium">
                  Commission Active
                </span>
              </div>
            </div>
          </div>
        </header>
  
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="flex flex-col lg:flex-row gap-8">
            {/* Sidebar Navigation */}
            <div className="lg:w-64">
              <nav className="bg-white rounded-lg shadow-md p-4">
                <ul className="space-y-2">
                  {[
                    { id: 'dashboard', label: 'Dashboard', icon: BarChart3 },
                    { id: 'elections', label: 'Elections', icon: Calendar },
                    { id: 'candidates', label: 'Candidates', icon: Users },
                    { id: 'create-election', label: 'Create Election', icon: Plus },
                    { id: 'add-candidate', label: 'Add Candidate', icon: Plus }
                  ].map(item => {
                    const Icon = item.icon;
                    return (
                      <li key={item.id}>
                        <button
                          onClick={() => setActiveTab(item.id)}
                          className={`w-full flex items-center space-x-3 px-3 py-2 rounded-lg text-left transition-colors ${
                            activeTab === item.id
                              ? 'bg-blue-100 text-blue-700 font-medium'
                              : 'text-gray-600 hover:bg-gray-100'
                          }`}
                        >
                          <Icon className="h-5 w-5" />
                          <span>{item.label}</span>
                        </button>
                      </li>
                    );
                  })}
                </ul>
              </nav>
            </div>
  
            {/* Main Content */}
            <div className="flex-1">
              {renderContent()}
            </div>
          </div>
        </div>
      </div>
    );
  };
  
  export default ElectionCommissionDashboard;